import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';
import { HttpClient } from '@angular/common/http';
import { AfterViewInit, ChangeDetectorRef, Directive, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { debounceTime, filter, Subject, Subscription } from 'rxjs';

@Directive()
export abstract class InfiniteScrollComponent<T = unknown> implements OnInit, AfterViewInit, OnDestroy {

  @ViewChild(CdkVirtualScrollViewport)
  readonly virtualScroll!: CdkVirtualScrollViewport;

  /*
   * Required property.
   */
  resourceUrl: string | undefined;

  pageSize = 20;
  loading = true;
  canLoadMore = true;
  loadMoreMargin = 0;
  items: T[] = [];

  readonly scrollStream$ = new Subject<void>();

  private totalCount = 0;
  private currentPage = 0;
  private container: HTMLElement | undefined;
  private scrollSubscription: Subscription | undefined;

  constructor(
    protected readonly http: HttpClient,
    protected readonly cdr: ChangeDetectorRef,
  ) {}

  ngOnInit(): void {
    if (!this.resourceUrl) {
      throw new Error('"resourceUrl" is required for the InfiniteScrollComponent');
    }

    this.loadPage(this.currentPage);
    this.scrollSubscription = this.subscribeToScroll();
  }

  ngAfterViewInit(): void {
    this.container = this.virtualScroll?.elementRef.nativeElement;
  }

  ngOnDestroy(): void {
    this.scrollSubscription?.unsubscribe();
  }

  // eslint-disable-next-line @typescript-eslint/no-empty-function
  protected onAfterItemsAdded(_newItems: T[]): void {}

  private shouldLoadMore(container: HTMLElement): boolean {
    const scrollTop = container.scrollTop + container.offsetHeight;

    return container.scrollHeight <= (scrollTop + this.loadMoreMargin);
  }

  private loadPage(page: number): void {
    const url = `${this.resourceUrl}?_start=${page * this.pageSize}&_limit=${this.pageSize}`;

    this.loading = true;

    this.http.get<T[]>(url, { observe: 'response' }).subscribe(response => {
      // Extract the total count at the first-page load only.
      if (page === 0) {
        const totalCount = response.headers.get('x-total-count');

        this.totalCount = totalCount ? +totalCount : 0;
      }

      if (response.body) {
        this.addItems(response.body);
      }
    });
  }

  private subscribeToScroll(): Subscription {
    return this.scrollStream$.pipe(
      debounceTime(200),
      filter(() => {
        if (this.loading || !this.container) {
          return false;
        }

        return this.shouldLoadMore(this.container);
      }),
    ).subscribe(() => {
      this.loadPage(++this.currentPage);
    });
  }

  private addItems(items: T[]): void {
    this.items = this.items.concat(items);
    this.loading = false;
    this.canLoadMore = this.totalCount > this.items.length;

    if (!this.canLoadMore) {
      this.scrollSubscription?.unsubscribe();
    }

    this.onAfterItemsAdded(items);
    this.cdr.detectChanges();
  }
}
