import { Directive, OnInit, TemplateRef, ViewContainerRef } from '@angular/core';

import { PlatformShieldService } from '../services/platform-shield.service';

@Directive({
  selector: '[browserRender]'
})
export class BrowserRenderDirective implements OnInit {

  constructor(
    private readonly platformShield: PlatformShieldService,
    private readonly viewContainer: ViewContainerRef,
    private readonly templateRef: TemplateRef<HTMLElement>,
  ) {}

  ngOnInit(): void {
    if (this.platformShield.isPlatformBrowser()) {
      this.viewContainer.createEmbeddedView(this.templateRef);
    } else {
      this.viewContainer.clear();
    }
  }

}
